import { useEffect } from 'react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {
	Button,
	Flex,
	Heading,
	Icon,
	Table,
	Thead,
	Tbody,
	Th,
	Tr,
	Td,
	Box,
} from '@chakra-ui/react';
import { IoPencilSharp, IoTrashBinSharp, IoAdd } from 'react-icons/io5';
import Loader from '../components/Loader';
import Message from '../components/Message';
import {
	listProducts,
	deleteProduct,
	createProduct,
} from '../actions/productActions';
import { PRODUCT_CREATE_RESET } from '../constants/productConstant';

const ProductListScreen = () => {
	const dispatch = useDispatch();
	const navigate = useNavigate();

	const productList = useSelector((state) => state.productList);
	const { loading, error, products } = productList;

	const userLogin = useSelector((state) => state.userLogin);
	const { userInfo } = userLogin; //to checkout if user logged-in or not  so userInfo

	const productDelete = useSelector((state) => state.productDelete);
	const {
		loading: loadingDelete,
		error: errorDelete,
		success: successDelete,
	} = productDelete;

	const productCreate = useSelector((state) => state.productCreate);
	const {
		loading: loadingCreate,
		error: errorCreate,
		success: successCreate,
		product: createdProduct, //we will also get our created product so 'created product'
	} = productCreate; //All the 4 values are destructred from state

	useEffect(() => {
		dispatch({ type: PRODUCT_CREATE_RESET });

		if (!userInfo.isAdmin) {
			navigate('/login');
		}

		if (successCreate) {
			navigate(`/admin/product/${createdProduct._id}/edit`); //if successCreate navigate to edit page of the product
		} else {
			dispatch(listProducts());
		}
	}, [
		dispatch,
		navigate,
		userInfo,
		successDelete,
		successCreate,
		createdProduct,
	]);
	//successDelete- it is added to see the updated value after deleleting

	const deleteHandler = (id) => {
		if (window.confirm('Are you sure')) {
			dispatch(deleteProduct(id));
		}
	};

	const createProductHandler = () => {
		dispatch(createProduct());
	};

	return (
		<>
			<Flex mb="5" alignItems="center" justifyContent="space-between">
				<Heading as="h1" fontSize="3xl" mb="5">
					Product
				</Heading>
				<Button onClick={createProductHandler} colorScheme="teal">
					<Icon as={IoAdd} mr="2" fontSize="xl" fontWeight="bold"></Icon>Create
					Product
				</Button>
			</Flex>

			{loadingDelete && <Loader />}
			{errorDelete && <Message type="error">{errorDelete}</Message>}

			{loadingCreate && <Loader />}
			{errorCreate && <Message type="error">{errorCreate}</Message>}

			{loading ? (
				<Loader />
			) : error ? (
				<Message type="error">{error}</Message>
			) : (
				<Box bgColor="white" rounded="lg" shadow="lg" px="5" py="5">
					<Table variant="stripped" colorScheme="gray" size="sm">
						<Thead>
							<Tr>
								<Th>ID</Th>
								<Th>NAME</Th>
								<Th>PRICE</Th>
								<Th>CATEGORY</Th>
								<Th>BRAND</Th>
								<Th></Th>
							</Tr>
						</Thead>
						<Tbody>
							{products.map((product) => (
								<Tr key={product._id}>
									<Td>{product._id}</Td>
									<Td>{product.name}</Td>
									<Td>{product.price}</Td>
									<Td>{product.category}</Td>
									<Td>{product.brand}</Td>
									<Td>
										<Flex justifyContent="flex-end" alignItems="center">
											<Button
												mr="4"
												as={RouterLink}
												to={`/admin/product/${product._id}/edit`}
												colorScheme="teal"
											>
												<Icon as={IoPencilSharp} color="white"></Icon>
											</Button>
											<Button
												mr="4"
												onClick={() => deleteHandler(product._id)}
												colorScheme="red"
											>
												<Icon as={IoTrashBinSharp} color="white"></Icon>
											</Button>
										</Flex>
									</Td>
								</Tr>
							))}
						</Tbody>
					</Table>
				</Box>
			)}
		</>
	);
};

export default ProductListScreen;
