import axios from 'axios';
import { useState, useEffect } from 'react';
import { Link as RouterLink, useNavigate, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {
	Button,
	Flex,
	Heading,
	FormControl,
	FormLabel,
	Input,
	Spacer,
	Link,
} from '@chakra-ui/react';
import Message from '../components/Message';
import Loader from '../components/Loader';
import FormContainer from '../components/FormContainer';
import { listProductDetails, updateProduct } from '../actions/productActions';
import { PRODUCT_UPDATE_RESET } from '../constants/productConstant';

const ProductEditScreen = () => {
	const dispatch = useDispatch();
	const navigate = useNavigate();
	const { id: productId } = useParams();

	const [name, setName] = useState('');
	const [price, setPrice] = useState(0);
	const [image, setImage] = useState(''); //product image url
	const [brand, setBrand] = useState('');
	const [category, setCategory] = useState('');
	const [description, setDescription] = useState('');
	const [countInStock, setCountInStock] = useState('');
	const [uploading, setUploading] = useState(false);

	const productDetails = useSelector((state) => state.productDetails);
	const { loading, error, product } = productDetails;

	const productUpdate = useSelector((state) => state.productUpdate);
	const {
		loading: loadingUpdate,
		error: errorUpdate,
		success: successUpdate,
	} = productUpdate;

	useEffect(() => {
		if (successUpdate) {
			dispatch({ type: PRODUCT_UPDATE_RESET });
			navigate(`/admin/productlist`);
		} else {
			if (!product.name || product._id !== productId) {
				dispatch(listProductDetails(productId));
			} else {
				setName(product.name);
				setPrice(product.price);
				setImage(product.image);
				setBrand(product.brand);
				setCategory(product.category);
				setCountInStock(product.countInStock);
				setDescription(product.description);
			}
		}
	}, [dispatch, navigate, productId, product, successUpdate]);

	const submitHandler = (e) => {
		e.preventDefault();
		dispatch(
			updateProduct({
				_id: productId,
				name,
				price,
				image,
				brand,
				category,
				description,
				countInStock,
			})
		);
	};

	const uploadFileHandler = async (e) => {
		const file = e.target.files[0]; //it will be array and we uploading one image so zeroth index
		const formData = new FormData(); //built in browser functionality just like new Date . formData is special object you create in browser that you send to server. any kindof file uploading is done using formData object
		formData.append('image', file); //the name should be same as backend name- router.post
		setUploading(true); //true- so that it can show loader

		try {
			const config = {
				headers: {
					'Content-Type': 'multipart/form-data', //write in the same way
				},
			};
			const { data } = await axios.post(`/api/uploads`, formData, config);
			setImage(data); //data will be the url of upload after we upload the image
			setUploading(false);
		} catch (error) {
			console.error(error);
			setUploading(false);
		}
	};

	return (
		<>
			<Link as={RouterLink} to="/admin/productlist">
				Go Back
			</Link>

			<Flex w="full" alignItems="center" justifyContent="center" py="5">
				<FormContainer>
					<Heading as="h1" mb="8" fontSize="3xl">
						Edit Product
					</Heading>

					{loadingUpdate && <Loader />}
					{errorUpdate && <Message type="error">{errorUpdate}</Message>}

					{loading ? (
						<Loader />
					) : error ? (
						<Message type="error">{error}</Message>
					) : (
						<form onSubmit={submitHandler}>
							{/* Name */}
							<FormControl id="name" isRequired>
								<FormLabel>Name</FormLabel>
								<Input
									type="text"
									placeholder="Enter Name"
									value={name}
									onChange={(e) => setName(e.target.value)}
								/>
							</FormControl>
							<Spacer h="3" />
							{/* PRICE */}
							<FormControl id="price" isRequired>
								<FormLabel>Price</FormLabel>
								<Input
									type="number"
									placeholder="Enter Price"
									value={price}
									onChange={(e) => setPrice(e.target.value)}
								/>
							</FormControl>
							<Spacer h="3" />

							{/* Image */}
							<FormControl id="image" isRequired>
								<FormLabel>Image</FormLabel>
								<Input
									type="text"
									placeholder="Enter image url"
									value={image}
									onChange={(e) => setImage(e.target.value)}
								/>
								<Input
									type="file"
									id="image-file"
									onChange={uploadFileHandler}
								/>
							</FormControl>
							<Spacer h="3" />

							{/* Description */}
							<FormControl id="description" isRequired>
								<FormLabel>Description</FormLabel>
								<Input
									type="text"
									placeholder="Enter description"
									value={description}
									onChange={(e) => setDescription(e.target.value)}
								/>
							</FormControl>
							<Spacer h="3" />

							{/* Brand */}
							<FormControl id="brand" isRequired>
								<FormLabel>Brand</FormLabel>
								<Input
									type="text"
									placeholder="Enter Brand Name"
									value={brand}
									onChange={(e) => setBrand(e.target.value)}
								/>
							</FormControl>
							<Spacer h="3" />

							{/* Category */}
							<FormControl id="category" isRequired>
								<FormLabel>Category</FormLabel>
								<Input
									type="text"
									placeholder="Enter Category"
									value={category}
									onChange={(e) => setCategory(e.target.value)}
								/>
							</FormControl>
							<Spacer h="3" />

							{/* CountInStock */}
							<FormControl id="countInStock" isRequired>
								<FormLabel>Count In Stock</FormLabel>
								<Input
									type="number"
									placeholder="Product in stock"
									value={countInStock}
									onChange={(e) => setCountInStock(e.target.value)}
								/>
							</FormControl>
							<Spacer h="3" />

							<Button
								type="submit"
								isLoading={loading}
								colorScheme="teal"
								mt="4"
							>
								{' '}
								Update
							</Button>
						</form>
					)}
				</FormContainer>
			</Flex>
		</>
	);
};

export default ProductEditScreen;
