import { useState, useEffect } from 'react';
import { Link as RouterLink, useNavigate, useParams } from 'react-router-dom';
import {
	Button,
	Flex,
	Heading,
	FormControl,
	FormLabel,
	Input,
	Spacer,
	Link,
	Checkbox,
} from '@chakra-ui/react';
import { useDispatch, useSelector } from 'react-redux';
import Message from '../components/Message';
import Loader from '../components/Loader';
import FormContainer from '../components/FormContainer';
import { getUserDetails, updateUser } from '../actions/userActions';
import { USER_UPDATE_RESET } from '../constants/userConstants';

const UserEditScreen = () => {
	const dispatch = useDispatch();
	const navigate = useNavigate();
	const { id: userId } = useParams();

	const [name, setName] = useState('');
	const [email, setEmail] = useState('');
	const [isAdmin, setIsAdmin] = useState(false);

	const userDetails = useSelector((state) => state.userDetails); //it will give us the details of the current users
	const { loading, error, user } = userDetails;

	const userUpdate = useSelector((state) => state.userUpdate); //after updatation we will read the value from the store
	const {
		loading: loadingUpdate,
		error: errorUpdate,
		success: successUpdate, //successUpdate- if true - whatever we have edited it is successful done
	} = userUpdate;

	/* useEffect
    if(successUpdate) - true - Whatever we have edited it is successfully done, so then we 
    we have to dispatch "RESET" because you have updated user details but store is still having the same details 
    so we have to reset it so that store get vacant. It is done on edit page after successful editing it will
    Navigate back to 'admin/userlist'
    - ELSE - if(successUpdate) is not truthy - we have not updated anything yet
            -if (!user.name || user._id !== userId) 
                    - If we visited page so the id in address bar if it doesnt match with the user.name and user.ID - 
                     - That means we are in the wrong user page then get the user details of the userid which we got from the useParams(address bar) 
    - ELSE - Above else condition is true and you have the details of correct user which you want edit   
                - Then setName(user.name);setEmail(user.email); fill the details of the correct person
    */

	useEffect(() => {
		if (successUpdate) {
			dispatch({ type: USER_UPDATE_RESET });
			navigate('/admin/userlist');
		} else {
			if (!user.name || user._id !== userId) {
				dispatch(getUserDetails(userId));
			} else {
				setName(user.name);
				setEmail(user.email);
				setIsAdmin(user.isAdmin);
			}
		}
	}, [user, dispatch, userId, successUpdate, navigate]);	
	const submitHandler = (e) => {
		e.preventDefault();
		dispatch(updateUser({ _id: userId, name, email, isAdmin }));
	};
	return (
		<>
			<Link as={RouterLink} to="/admin/userlist">
				Go Back
			</Link>
			<Flex w="full" alignItems="center" justifyContent="center" py="5">
				<FormContainer>
					<Heading as="h1" mb="8" fontSize="3xl">
						Edit User
					</Heading>

					{/* Update related details  so loading update if to try somethign and any issue is there it print the values*/}
					{loadingUpdate && <Loader />}
					{errorUpdate && <Message type="error">{errorUpdate}</Message>}

					{/* original details */}
					{loading ? (
						<Loader />
					) : error ? (
						<Message type="error">{error}</Message>
					) : (
						<form onSubmit={submitHandler}>
							<FormControl id="name" isRequired>
								<FormLabel>Name</FormLabel>
								<Input
									type="text"
									placeholder="Enter full name"
									value={name}
									onChange={(e) => setName(e.target.value)}
								/>
							</FormControl>
							<Spacer h="3" />

							<FormControl id="email" isRequired>
								<FormLabel>Email</FormLabel>
								<Input
									type="email"
									placeholder="Enter full name"
									value={email}
									onChange={(e) => setEmail(e.target.value)}
								/>
							</FormControl>
							<Spacer h="3" />

							<FormControl id="isAdmin" isRequired>
								<FormLabel>Is Admin?</FormLabel>
								<Checkbox
									size="lg"
									colorScheme="teal"
									checked={isAdmin}
									onChange={(e) => setIsAdmin(e.target.checked)}
								>
									isAdmin?
								</Checkbox>

								<Spacer h="3" />

								<Button
									type="submit"
									isLoading={loading}
									colorScheme="teal"
									mt="4"
								>
									{' '}
									Update
								</Button>
							</FormControl>
						</form>
					)}
				</FormContainer>
			</Flex>
		</>
	);
};

export default UserEditScreen;
